export default "@import \"../../../../src/styles/nui-framework-variables\";\n@rectHeight: 25px;\n@rectShadow: rgba(17, 17, 17, 0.3);\n.examples {\n    color: #FF0000;\n    font-family: \"Open Sans\";\n    font-size: 12px;\n\n    width: 150px;\n\n    .black {\n        color: @black;\n    }\n\n    .examples-font-weight {\n        width: 300px;\n        .font-weight {\n            &-light {\n                width: inherit;\n                font-weight: @nui-font-weight-light;\n            }\n            &-regular {\n                width: inherit;\n                font-weight: @nui-font-weight-regular;\n            }\n            &-semibold {\n                width: inherit;\n                font-weight: @nui-font-weight-semibold;\n            }\n            &-bold {\n                width: inherit;\n                font-weight: @nui-font-weight-bold;\n            }\n        }\n    }\n\n    .examples-font-size {\n        width: 300px;\n        .font-size {\n            &-hero {\n                width: inherit;\n                font-size: @nui-font-size-hero;\n            }\n            &-big {\n                width: inherit;\n                font-size: @nui-font-size-big;\n            }\n            &-default {\n                width: inherit;\n                font-size: @nui-font-size-default;\n            }\n            &-small {\n                width: inherit;\n                font-size: @nui-font-size-small;\n            }\n        }\n    }\n\n    .examples-icon-size {\n        width: 300px;\n        .icon-size {\n            &-medium {\n                width: inherit;\n                height: @icon-size-medium;\n                width: @icon-size-medium;\n                border: 1px solid #FF0000;\n            }\n            &-default {\n                width: inherit;\n                height: @icon-size-default;\n                width: @icon-size-default;\n                border: 1px solid #FF0000;\n            }\n            &-small {\n                width: inherit;\n                height: @icon-size-small;\n                width: @icon-size-small;\n                border: 1px solid #FF0000;\n            }\n        }\n    }\n\n    .space {\n        &-m {\n            background-color: rgba(100%, 0, 0, 0.2);\n            width: @nui-space-md;\n            height: @rectHeight;\n        }\n        &-s {\n            background-color: rgba(97.6%,61.6%,11%, 0.2);\n            width: @nui-space-sm;\n            height: @rectHeight;\n        }\n        &-xs {\n            background-color: rgba(18.8%,69.8%,18.8%, 0.2);\n            width: @nui-space-xs;\n            height: @rectHeight;\n        }\n        &-xxs {\n            background-color: rgba(0%,59.2%,92.2%, 0.2);\n            width: @nui-space-xxs;\n            height: @rectHeight;\n        }\n    }\n\n    .examples-line-thickness {\n        width: 150px;\n        .line {\n            width: 100px;\n            background-color: #FF0000;\n            &-default {\n                height: 1px;\n            }\n            &-medium{\n                height: 2px;\n            }\n            &-thick {\n                height: 3px;\n            }\n            &-fat {\n                height: 4px;\n            }\n        }\n    }\n\n    .rect-with-radius {\n        background-color: #FF0000;\n        border-radius: @nui-radius-default;\n        width: 30px;\n        height: 30px;\n    }\n}\n.spacing-hints {\n    .color-black {\n        color: black;\n    }\n}\n\n.shadow-rectangle {\n    width: 300px;\n    height: 100px;\n    margin: 10px;\n    padding: 20%;\n    &.shadow-default {\n        box-shadow: @nui-shadow-default;\n    }\n    &.shadow-elevated {\n        box-shadow: @nui-shadow-elevated;\n    }\n    &.shadow-stronger {\n        box-shadow: @nui-shadow-stronger;\n    }\n    &.shadow-inner {\n        box-shadow: @nui-shadow-inner;\n    }\n    &.shadow-inner-top {\n        box-shadow: @nui-shadow-inner-top;\n    }\n    &.shadow-inner-top-bottom {\n        box-shadow: @nui-shadow-inner-top-bottom;\n    }\n    &.shadow-inner-bottom {\n        box-shadow: @nui-shadow-inner-bottom;\n    }\n    &.shadow-inner-left {\n        box-shadow: @nui-shadow-inner-left;\n    }\n    &.shadow-inner-right {\n        box-shadow: @nui-shadow-inner-right;\n    }\n    &.shadow-inner-left-right {\n        box-shadow: @nui-shadow-inner-left-right;\n    }\n    &.scroll-shadow {\n        height: 150px;\n        overflow-y: auto;\n        overflow-x: hidden;\n        padding: 2%;\n    }\n    &.horizontal {\n        padding: 2%;\n        overflow-y: hidden;\n        overflow-x: auto;\n        white-space: nowrap;\n    }\n}\n.example-text {\n    width: 500px;\n}\n";